<template>
<div>
    <NavBar/>
  <div>
    <div>
      <Alert/>
    </div>
    <transition name="slide-fade">
      <router-view id="view" tag="div" :class="{container:isContainer}"></router-view>
    </transition>
  </div>
</div>

</template>

<script>
import NavBar from './components/NavBar'
import router from './router/index'
import Alert from './components/Alert'
export default {
  name: 'App',
  components:{NavBar,Alert},
  data(){
    return{
      isContainer:true

    }
  },
  mounted(){
    const widthPx = document.documentElement.clientWidth
    const view = document.querySelector('#view')
    if(widthPx<768){
      this.isContainer = false
    }
  }
}
</script>

<style>
  html,body{
    width: 100%;
    /* height: 100%;
    overflow: hidden; */
    background: #000000;  

  }
  #view{
    margin-top: 60px;
    height: auto;
    overflow: hidden;

  }


  .slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all .2s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to {
  animation: move .5s;
}
@keyframes move {
  0%{
    transform: scale(0.5);   
  }
  50%{
    transform: scale(1);   
  }
  100%{
     transform: scale(1);  
     opacity: 0;
  }
}
</style>



