    export default {
		name:'NavBar',
		data(){
			return{
				qq:'438519233',
				wx:'13550190560',
				navbar:{
					navbar1:'LINK',
					navbar2:'首页',
					navbar3:'技术栈',
					navbar4:'联系',
					navbar5:'分享',
					navbar6:'笔记',
					navbar7:'ADMIN'

				},
				isContainer:true
			}
		},
		mounted(){
			const wpx = document.documentElement.clientWidth
			const navbar = document.querySelector('#navbar')
			if(wpx<768){
				this.isContainer = false
			}
		}
    }
</script>

