<template>
    <div>
        <div v-for="list in lists" :key="list.item">   
            <div class="list-group" >
                <a href="#" class="list-group-item active">
                    <h4 class="list-group-item-heading">
                        {{list.title}}
                    </h4>
                </a>
                <a href="#" class="list-group-item">
                    <div class="list-group-item-heading">
                        {{list.item}}
                    </div>
                </a>
            </div>
        </div>  
    </div>
</template>

<script>
export default {
  name: "About",
  data() {
    return {
      lists: [
        { title: "简介", item: "菜鸟一名,前后端/设计都有一定了解,还请大佬指导指导" },
        { title: "框架", item: "vue react jquery bootstrap element-ui antd-moblie django express" },
        { title: "技术栈", item: "vueJs + vuex + vueRouter + ajax+ python + django + mysql" },
        { title: "技术栈", item: "reactJs + reactRedux + reactRouter + ajax+ nodeJs + express+ mongodb" },
        { title: "技术栈", item: "html5 + css3 + javascript(es6) + ajax + php + mysql" },
      ]
    };
  }
};
</script>

<style scoped>
    .list-group{
       border-radius: 0px;
    }
</style>


