<template>
    <div>
        <h1 class="found">{{msg}}</h1>
    </div>
</template>

<script>
    export default {
        name:'NotFound',
        data(){
            return{
                msg:'404,页面失联了'
            }
        }
    }
</script>

<style scoped>
.found{
    display: inline-block;
    width: 100%;
    height: 100%;
    text-align: center;
    line-height: 200px;    
}
</style>


